.algorithmDetail-container .algorithmDetail-left-tree {
  position: absolute;
  top: 50px;
  width: 22%;
  overflow: auto;
  bottom: 0;
  left: 0;
  border-right: 1px solid #ddd;
  padding: 1% 0.5% 1% 1%;
}
.algorithmDetail-container .algorithmDetail-left-tree .algorithmDetail-title {
  width: 100%;
  height: 30px;
  line-height: 30px;
  display: inline-block;
  padding-left: 5px;
  font-family: ' Bold', '';
  font-style: normal;
  font-size: 14px;
  color: #333;
  background-color: #d8dde6;
  border-radius: .125rem;
}
.algorithmDetail-container .algorithmDetail-right-content {
  position: absolute;
  top: 50px;
  overflow: auto;
  bottom: 0;
  left: 22%;
  right: 0;
  padding-left: 30px;
  padding-right: 10px;
}
.algorithmDetail-container .algorithmDetail-right-content strong {
  font-weight: 700 !important;
}
.algorithmDetail-container .algorithmDetail-right-content .algorithmDetail-content-title {
  font-family: ' Bold', '';
  font-weight: 700;
  font-style: normal;
  font-size: 20px;
  color: #666666;
  text-align: left;
  display: block;
  width: 100%;
