}
.algorithmDetail-container .algorithmDetail-right-content .content-item {
  padding: 20px 0 20px 0;
  border-bottom: 1px solid #ddd;
}
.algorithmDetail-container .algorithmDetail-right-content .content-item .content-item-title {
  margin-bottom: 20px;
}
.algorithmDetail-container .algorithmDetail-right-content .content-item .content-item-title span {
  font-family: ' Bold', '';
  font-weight: 700;
  font-style: normal;
  font-size: 18px;
  color: #666666;
  text-align: left;
  line-height: normal;
}
.algorithmDetail-container .algorithmDetail-right-content .content-item .content_inner {
  padding: 0 20px;
