.algorithmDetail-container:after {
  content: '.';
  display: block;
  visibility: hidden;
  clear: both;
  height: 0;
}
.tree li {
  border-radius: 4px;
}
.tree li ul {
  position: relative;
  overflow: hidden;
  -webkit-transition: height 0.6s;
  -o-transition: height 0.6s;
  transition: height 0.6s;
}
.tree li > a {
  font-family: \\5FAE\8F6F\96C5\9ED1 Regular,\\5FAE\8F6F\96C5\9ED1;
  font-weight: 400;
  font-style: normal;
  font-size: 13px;
  color: #333;
  text-align: left;
  white-space: nowrap;
  display: block;
  width: 100%;
  position: relative;
  cursor: default;
  height: 40px;
  line-height: 40px;
  padding-left: 20px;
}
.tree li > a:hover {
  text-decoration: none;
}
.tree li > a:focus {
  text-decoration: none;
}
.tree li > ul li a {
  font-family: \\5FAE\8F6F\96C5\9ED1 Regular,\\5FAE\8F6F\96C5\9ED1;
  font-weight: 400;
  font-style: normal;
  font-size: 0.75rem;
  color: #666;
  text-align: left;
  white-space: nowrap;
  display: block;
  width: 100%;
  position: relative;
  cursor: default;
  height: 40px;
  line-height: 40px;
  padding-left: 40px;
}
.treemenu li {
  list-style: none;
  position: relative;
}
.treemenu .toggler {
  cursor: pointer;
}
.treemenu .toggler:hover {
  background-color: #ecf6fd;
}
.treemenu .actived {
  background-color: #ecf6fd;
}
.tree-closed .toggler:after {
  display: inline-block;
  position: absolute;
  content: "";
  right: 10px;
  background: url("../../../public/index_login/to-r.png") no-repeat center;
  width: 9px;
  height: 9px;
  background-size: 15px 15px;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}
.tree-opened > .toggler:after {
  display: inline-block;
  position: absolute;
  content: "";
  right: 10px;
  background: url("../../../public/index_login/to-b.png") no-repeat center;
  width: 9px;
  height: 9px;
  background-size: 15px 15px;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
}
.tree-empty > .toggler:after {
  display: none;
}
.tree li > ul > li > span {
  font-size: 14px;
}
.tree .active {
  color: #0099ff;
}



