.nav-wrapper {
  width: 100%;
  height: 3.125rem;
  padding: 0.3125rem 2%;
  position: fixed;
  z-index: 1000;
}
.inline {
  display: inline-block;
}
.nav-banner {
  width: 40px;
}
.banner-logo {
  width: 2.5rem;
  height: 2.5rem;
}
.banner-title {
  padding-left: 2%;
  margin: auto;
  font-family: ' Regular', '';
  font-weight: 700;
  font-style: normal;
  font-size: 1rem;
  color: white;
}
.nav-menu {
  width: 60%;
  padding: 0px;
  color: white;
  text-align: left;
  display: inline-block;
}
.nav-menu li {
  padding: 0 20px;
  display: inline-block;
}
.nav-menu li a {
  color: white;
  font-size: 15px;
  text-decoration: none;
}
.nav-menu li a:hover {
  font-size: 15px;
  font-weight: 700;
}
.nav-normal {
  width: 36%;
  text-align: right;
  display: inline-block;
}
.nav-normal li {
  display: inline-block;
}
.nav-normal li a {
  padding: 0 20px;
  color: white;
  position: relative;
  text-decoration: none;
}
.nav-normal li a:hover {
  font-size: 14px;
  font-weight: 700;
  display: inline-block;
}
.nav-normal li .user-setting-none {
  top: 20px;
  width: 100%;
  float: right;
  position: relative;
  text-align: center;
  display: none;
}
.nav-normal li .user-setting-none a {
  height: 40px;
  display: block;
  background-color: white;
  color: grey;
  line-height: 40px;
  text-decoration: none;
}
.nav-normal li .user-setting-flex {
  top: 20px;
  width: 100%;
  float: right;
  position: relative;
  text-align: center;
  display: block;
  z-index: 9999;
}
.nav-normal li .user-setting-flex a {
  height: 40px;
  background-color: white;
  color: grey;
  line-height: 40px;
  text-decoration: none;
  display: block;
}
.nav-drop {
  top: 49px;
  width: 100%;
  height: 294px;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  z-index: 1000;
  padding-left: 14%;
  background-size: cover;
}
.nav-drop ul {
  padding: 1%;
  display: inline-block;
  vertical-align: top;
}
.nav-drop ul li {
  padding: 1%;
}
.nav-drop ul li a {
  color: white;
  text-decoration: none;
}



