.problem_container {
  width: 100%;
  min-width: 1000px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.problem_container .problem_content {
  width: 1000px;
  position: relative;
  margin-top: 50px;
  min-height: 500px;
}
.problem_container .problem_content .problem_left_menu {
  position: fixed;
  left: 0;
  top: 50px;
  width: 205px;
  padding: 15px 20px 20px 20px;
}
.problem_container .problem_content .problem_left_menu .problem_line {
  height: 490px;
  border: 1px solid #ddd;
  border-collapse: collapse;
  position: fixed;
  top: 50px;
  left: 205px;
}
.problem_container .problem_content .problem_left_menu .problem_detail {
  margin-bottom: 20px;
}
.problem_container .problem_content .problem_left_menu .problem_detail .problem_title {
  width: 100%;
  height: 21px;
  line-height: 21px;
  text-align: left;
  margin-bottom: 20px;
}
.problem_container .problem_content .problem_left_menu .problem_detail .problem_title span {
  font-family: ' Regular', '';
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  color: #666666;
  text-align: left;
  line-height: normal;
}
.problem_container .problem_content .problem_left_menu .problem_detail .problem_detail_content {
  width: 100%;
  padding-left: 14px;
}
.problem_container .problem_content .problem_left_menu .problem_detail .problem_detail_content li {
  display: inline-block;
  font-family: ' Regular', '';
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  color: #0099FF;
  text-align: left;
  line-height: normal;
  margin-bottom: 18px;
}
.problem_container .problem_content .problem_right_content {
  position: absolute;
  top: 0;
  right: 0;
  padding-top: 33px;
  padding-left: 15px;
  width: 795px;
  min-height: 500px;
}
.problem_container .problem_content .problem_right_content .problem_common_title {
  width: 100%;
  height: 26px;
  line-height: 26px;
  text-align: left;
  margin-bottom: 24px;
}
.problem_container .problem_content .problem_right_content .problem_common_title span {
  font-family: ' Bold', '';
  font-weight: 700;
  font-style: normal;
  font-size: 20px;
  color: #666666;
  text-align: left;
  line-height: normal;
}
.problem_container .problem_content .problem_right_content .problem_common_content {
  padding-left: 40px;
}
.problem_container .problem_content .problem_right_content .problem_common_content p {
  font-family: ' Regular', '';
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  color: #666666;
  text-align: left;
  height: 19px;
  line-height: 19px;
  margin-bottom: 24px;
}
.problem_container .problem_content .problem_list .problem_details {
  margin-top: 35px;
}
.problem_container .problem_content .problem_list .problem_details .problem_details_title {
  height: 24px;
  line-height: 24px;
  font-family: ' Bold', '';
  font-weight: 700;
  font-style: normal;
  font-size: 18px;
  color: #666666;
  text-align: left;
}
.problem_container .problem_content .problem_list .problem_detail {
  margin-top: 20px;
}
.problem_container .problem_content .problem_list .problem_detail .problem_detail_title {
  height: 21px;
  line-height: 21px;
  font-family: ' Bold', '';
  font-weight: 700;
  font-style: normal;
  font-size: 16px;
  color: #666666;
  text-align: left;
}
.problem_container .problem_content .problem_list .problem_detail .problem_detail_content {
  margin-top: 6px;
  padding-left: 40px;
}
.problem_container .problem_content .problem_list .problem_detail .problem_detail_content p {
  font-family: ' Regular', '';
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  color: #666;
  text-align: left;
  line-height: 3;
  width: 600px;
  margin: 0;
}
.problem_container .problem_content:after {
  content: '.';
  display: block;
  height: 0;
  visibility: hidden;
  clear: both;
}



