.product.docker {
  position: relative;
  /* &.left-menu-item {
    padding: 5px 0;
  }*/
}
.product.docker.main-body {
  font-family: ' Regular', '';
  font-size: 14px;
  color: #666666;
}
.product.docker.main-title {
  text-align: center;
  font-size: 18px;
  font-weight: bold;
}
.product.docker.desc-text,
.product.docker.explain-text {
  float: left;
  margin: 40px 6% 0px 6%;
}
.product.docker.array-table {
  margin: 40px 0 0 0;
  width: 100%;
  height: auto;
  border: 1px #DCDCDC solid;
  padding: 10px 15px;
}
.product.docker.middle-line {
  margin-top: 25px;
}
.product.docker.left-menu-item-title .font.active {
  color: #0099FF;
}
.product.docker.left-menu-item-title span {
  margin-left: 5px;
  font-size: 16px;
  cursor: pointer;
}
.product.docker.left-menu-item-title img {
  float: right;
  /* margin-left:10px;*/
  margin-top: 5px;
  width: 15px;
  height: 15px;
}
.product.docker.right-content ul {
  float: none;
  padding: 20px 20px 20px 20px;
}
.product.docker.right-content ul li {
  list-style-type: disc;
  padding: 10px 0;
}
.product.docker.scenes-tabs {
  margin: 40px 0 0 0;
  border: 1px #DCDCDC solid;
}
.product.docker.top-tabs {
  height: 40px;
}
.product.docker.top-tabs .tab-item {
  float: left;
  height: 100%;
  padding-top: 1%;
  text-align: center;
  font-size: 16px;
  border-bottom: 1px #DCDCDC solid;
  cursor: pointer;
}
.product.docker.top-tabs .tab-item.index31,
.product.docker.top-tabs .tab-item.index41 {
  border: 1px #DCDCDC solid;
  border-top: none;
}
.product.docker.top-tabs .tab-item.index42 {
  border: 1px #DCDCDC solid;
  border-left: none;
  border-top: none;
}
.product.docker.top-tabs .tab-item.switch {
  border-bottom: none;
  color: #0099FF;
}
.product.docker.bottom-content {
  padding: 10px 60px 20px 60px;
}
.product.docker.docs-content {
  margin: 40px 0;
}
.product.docker.docs-content-item {
  float: left;
}
.product.docker.docs-content-item p {
  font-size: 15px;
  font-weight: bold;
  margin-bottom: 22px;
}
.product.docker.docs-content-item a {
  text-decoration: none;
  color: #666666;
}
.product.docker.docs-content-item a:hover {
  color: #0099FF;
}



