.index.index-products.main-wrapper {
  width: 100%;
  padding: 2%;
  background-color: white;
}
.index.index-products.main-wrapper .products-title {
  width: 100%;
  padding: 3% 3% 2% 3%;
  display: inline-block;
}
.index.index-products.main-wrapper .products-title .products-text {
  font-family:  Bold ;
  font-weight: 700;
  font-style: normal;
  font-size: 20px;
  color: #666666;
  display: inline;
  padding-left: 10px;
  vertical-align: middle;
}
.index.index-products.main-wrapper .products-wrapper {
  width: 100%;
  height: 400px;
  padding: 3%;
  display: inline-block;
}
.index.index-products.main-wrapper .products-wrapper .products-item {
  width: 16%;
  float: left;
  text-align: center;
  height: 350px;
  border: 1px solid lightgrey;
  padding: 1%;
  margin-top: 16px;
  margin-bottom: 16px;
}
.index.index-products.main-wrapper .products-wrapper .products-item.hover {
  width: 16%;
  float: left;
  text-align: left;
  height: 382px;
  padding: 1%;
  border: 1px solid lightgrey;
  margin: auto 0px;
}
.index.index-products.main-wrapper .products-wrapper .products-item .item-title {
  font-family: ' Regular', '';
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  color: #666666;
}
.index.index-products.main-wrapper .products-wrapper .products-item .item-title.hover {
  font-family: ' Regular', '';
  font-weight: bold;
  font-style: normal;
  font-size: 18px;
  color: #666666;
  display: inline;
}
.index.index-products.main-wrapper .products-wrapper .products-item .item-name {
  font-family: ' Regular', '';
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  color: #999999;
}
.index.index-products.main-wrapper .products-wrapper .products-item .item-name.hover {
  text-align: left;
  margin: auto 0px;
  font-family: ' Regular', '';
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  color: #666666;
}
.index.index-products.main-wrapper .products-wrapper .products-item .item-text {
  text-align: left;
  margin: auto 0px;
  font-family: ' Regular', '';
  font-weight: 400;
  font-style: normal;
  font-size: 12px;
  color: #999999;
}



