.data-mining-header {
  margin: 0 4%;
  border: 1px solid lightgrey;
}
.data-mining-header div {
  width: 100%;
  padding: 0% 2%;
  display: inline-block;
  color: #666666;
  font-size: 16px;
  font-weight: 700;
  font-style: normal;
  font-family: ' Bold', '';
}
.data-mining-header img {
  width: 40px;
  height: 40px;
}
.data-mining-header p {
  padding-left: 2%;
  display: inline-block;
}
.data-mining-body {
  margin: 0% 4%;
  border: 1px solid lightgrey;
  border-top: 0px;
}
.data-mining-body a {
  color: grey;
  text-decoration: none;
}
.data-mining-body a p {
  text-align: center;
}
.data-mining.hb-wrapper {
  width: 100%;
  padding: 1% 4%;
  text-align: center;
}
.data-mining.hb-wrapper .hb-bt {
  width: 45%;
  display: inline-block;
  border: 1px solid lightgrey;
  line-height: 100%;
  position: relative;
}
.data-mining.hb-wrapper .hb-bt:hover {
  border: 1px solid #5389d2;
}
.data-mining.hb-wrapper .hb-bt:hover .hb-spn {
  color: #5389d2;
}
.data-mining.hb-wrapper .hb-bt a {
  text-decoration: none;
}
.data-mining.hb-wrapper .hb-bt .hb-spn {
  font-family: \\5FAE\8F6F\96C5\9ED1 Regular,\\5FAE\8F6F\96C5\9ED1;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  color: #666;
  display: inline-block;
  height: 60px;
  line-height: 60px;
  width: 100%;
  cursor: pointer;
}
.data-summer-container {
  width: 100%;
  position: relative;
  overflow: hidden;
  padding: 2.5% 8%;
}
.data_al_linker:hover {
  color: #0069A8;
}



