.doc-header {
  width: 100%;
  height: 150px;
}
.doc-header {
  position: relative;
}
.doc-header .epm.row {
  margin: 0;
}
.doc-header .font,
.doc-header .input,
.doc-header button {
  float: left;
}
.doc-header .font {
  margin: 40px 10px 0 0;
}
.doc-header .input {
  width: 55%;
  margin-top: 50px;
}
.doc-header .font span {
  font-weight: 700;
  font-style: normal;
  font-size: 36px;
  color: #FFFFFF;
}
.doc-header .input input {
  width: 100%;
  height: 40px;
  font-weight: 400;
  font-size: 14px;
  text-decoration: none;
  color: #333;
}
.doc-header button {
  margin-top: 50px;
  width: 80px;
  height: 40px;
  background: #3385ff;
  border: 1px solid #3385ff;
  color: white;
  border-radius: 0;
  outline: medium;
}



