.doc-middle-tabs {
  padding: 20px 0 5px 0;
  border-bottom: 1px solid #CCCCCC;
}
.doc-middle-tabs {
  position: relative;
  width: 100%;
}
.doc-middle-tabs .epm.row {
  margin: 0;
}
.doc-middle-tabs span {
  font-family: ' Regular', '';
  font-weight: 400;
  font-style: normal;
  font-size: 16px;
  color: #666666;
  padding-bottom: 5px;
  cursor: pointer;
}
.doc-middle-tabs .click {
  color: #0099ff;
  border-bottom: 2px solid #0099FF;
}



