.swiper.item.item-wrapper {
  width: 100%;
  height: 100%;
  color: #fff;
  float: left;
  padding: 0.2rem;
  background-size: cover;
}
.swiper.item .content {
  width: 741px;
  padding-top: 4%;
  padding-left: 10%;
  position: relative;
}
.swiper.item .content .title {
  text-align: left;
  font-weight: 400;
  font-style: normal;
  font-size: 44px;
}
.swiper.item .content .text {
  text-align: left;
  line-height: normal;
}
.swiper.item .content .button {
  color: #fff;
  outline: none;
  border: 1px solid #fff;
  cursor: pointer;
  margin-top: 30px;
  font-size: 16px;
  font-weight: 600;
  float: left;
  height: 40px;
  margin-right: 25px;
  background: transparent;
  -webkit-transition: color 0.3s ease-in-out, background 0.3s ease-in-out;
  -o-transition: color 0.3s ease-in-out, background 0.3s ease-in-out;
  transition: color 0.3s ease-in-out, background 0.3s ease-in-out;
}
.swiper.item .content .button:hover {
  color: #373d41;
  background: #fff;
  border: 1px solid #fff;
}



