.product.details.navigation {
  position: fixed;
  top: 0;
  z-index: 9999;
  width: 100%;
  height: 60px;
  padding-left: 80px;
  background-image: url("../../../../public/product_logout/docker/u1.png");
}
.product.details.navitem {
  position: relative;
  float: left;
  height: 60px;
  padding: 15px 0;
  text-align: center;
  font-family: ' Regular', '';
  font-size: 16px;
  color: #FFFFFF;
  cursor: default;
}
.product.details.navitem hr {
  margin-top: 7px;
  *margin: 0;
  border: 0;
  color: black;
  background-color: #0099FF;
  width: 95%;
  margin-left: 2.5%;
  height: 2px;
}
.product.details .menu {
  background-image: url("../../../../public/common_logout/u202.png");
  height: 260px;
  width: 940px;
  margin-top: -1px;
}
.product.details .item-ul {
  float: left;
  width: 14.28571429%;
  padding: 0 0 0 2%;
}
.product.details .item-ul li {
  padding: 8px 0;
  cursor: pointer;
}
.product.details .item-ul .title {
  font-family: ' Bold', '';
  font-weight: 700;
  font-style: normal;
  font-size: 14px;
  color: #FFFFFF;
}
.product.details .item-ul li a {
  font-family: ' Regular', '';
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  color: #E4E4E4;
  text-decoration: none;
}
.product.details .item-ul li a:hover {
  color: #FFFFFF;
}



