.data-product.data-pro-details {
  position: relative;
  color: #666666;
  font-family: ' Regular', '';
  font-size: 14px;
  width: 100%;
}
.data-product.data-pro-details.item {
  padding: 40px 0;
}
.data-product.data-pro-details.main-text1 {
  padding: 20px 10%;
}
.data-product.data-pro-details.top-item {
  float: left;
  text-align: center;
}
.data-product.data-pro-details.top-item img {
  width: 40px;
  height: 40px;
  margin-bottom: 20px;
}
.data-product.data-pro-details.top-item a {
  text-decoration: none;
  color: #666666;
  font-size: 16px;
}
.data-product.data-pro-details.top-item a:hover {
  color: #0099FF;
}
.data-product.data-pro-details.desc1 {
  background-image: url("../../../public/product_logout/data/u96.png");
  padding: 10px 14%;
}
.data-product.data-pro-details.desc-item {
  float: left;
  margin: 0 3%;
}
.data-product.data-pro-details.desc-item a {
  color: #666666;
  text-decoration: none;
  font-weight: 400;
}
.data-product.data-pro-details.desc-item a:hover {
  color: #0099FF;
}
.data-product.data-pro-details.desc_content {
  margin-top: 10px;
  font-size: 13px;
  color: #999999;
  height: 72px;
}



