.pro_details_content img {
  max-width: none;
}
strong {
  font-weight: 700 !important;
}
.show_around_img {
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1003;
  background-color: rgba(0, 0, 0, 0.7);
}
.show_around_img img {
  position: relative;
  margin: 0 auto;
  left: 0;
  right: 0;
  top: 20%;
  z-index: 999;
  display: block;
  max-width: none;
}
.show_around_img .close {
  position: fixed;
  top: 6%;
  margin-right: 5%;
  float: right;
}



