.epm.layout {
  position: relative;
  height: 100%;
}
.epm.layout > .header {
  width: 100%;
  height: 64px;
  line-height: 64px;
}
.epm.layout > .header.fixed {
  position: fixed;
  z-index: 9999;
}
.epm.layout > .content {
  height: 100%;
}
.epm.layout > .sider {
  height: 100%;
  position: absolute;
  overflow: hidden;
}
.epm.layout > .sider:hover {
  overflow: auto;
}
.epm.layout > .sider.left {
  left: 0;
  top: 0;
}
.epm.layout > .sider.right {
  right: 0;
  top: 0;
}
.epm.layout > .footer {
  width: 100%;
  height: 64px;
}
.epm.layout.fullScreen {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.epm.layout.fullScreen > .content {
  position: absolute;
  width: 100%;
  height: auto;
  top: 0;
  bottom: 0;
}
.epm.navigation {
  background: #fff;
  color: #16325c;
  min-height: 3.25rem;
  height: auto;
  display: table;
  width: 100%;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}
.epm.navigation.fixed {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99999;
}
.epm.navigation .header {
  display: table-cell;
  vertical-align: middle;
}
.epm.navigation .header .logo {
  padding: 0 1.5rem;
  height: 3.25rem;
  line-height: 3.25rem;
  font-size: 1.5rem;
  font-weight: 700;
}
.epm.navigation .header img {
  width: 2.5rem;
  height: 2.5rem;
  margin: -0.25rem 0.75rem 0 0;
}
.epm.navigation .hamburger {
  display: table-cell;
  vertical-align: middle;
}
.epm.navigation .hamburger >.epm.dropdown {
  width: 2rem !important;
  float: right;
  margin-right: 10px;
}
.epm.navigation .hamburger >.epm.dropdown .collapsed {
  display: block !important;
  position: relative;
  float: right;
}
.epm.navigation .hamburger >.epm.dropdown >.wrap.bottomLeft {
  right: 0;
  top: 2rem;
  min-width: 200px;
}
.epm.navigation .hamburger >.epm.dropdown >.wrap.bottomLeft .trigger i.icon {
  position: static !important;
}
.epm.navigation .nav {
  display: table-cell !important;
}
.epm.navigation .nav:before {
  content: " ";
  display: table;
}
