.comm-menu-wrapper {
  width: 100%;
  display: table;
  cursor: pointer;
}
.comm-menu-wrapper-hl {
  width: 100%;
  display: table;
  cursor: pointer;
  background-color: #ecf6fd;
}
.comm-menu-wrapper:hover {
  background-color: #ecf6fd;
}
.comm-menu-ul {
  padding: 0px;
  margin: 0;
  list-style-type: none;
}
.comm-menu-i-arrow {
  position: relative;
  display: table-cell;
  padding-right: 10px;
}
.comm-menu-i-icon {
  width: 20px;
  height: 20px;
  margin: 0px;
  padding: 0px;
  padding-right: 5px;
  position: relative;
}
.comm-menu-i-span {
  position: relative;
  display: table-cell;
}
.comm-menu-list {
  width: 93%;
  height: 40px;
  line-height: 40px;
  padding-left: 20px;
  font-size: 13px;
  display: table-cell;
  position: relative;
  text-decoration: none;
  color: #333;
}
.comm-menu-list-hl {
  width: 93%;
  height: 40px;
  line-height: 40px;
  padding-left: 20px;
  font-size: 13px;
  display: table-cell;
  position: relative;
  text-decoration: none;
  background-color: #ecf6fd;
}
.comm-menu-sub-closed {
  display: none;
}
.comm-menu-sub-expanded {
  display: block;
}
.comm-menu-list:hover {
  text-decoration: none;
  background-color: #ecf6fd;
}
.epm.grey.tag {
  color: #333;
  font-size: 14px;
}



