.pro_docs {
  position: relative;
  font-family: ' Regular', '';
  font-size: 14px;
  color: #666666;
  padding: 0 3%;
}
.pro_docs.item {
  padding: 0;
}
.pro_docs.item.main_item {
  float: left;
  margin-top: 40px;
}
.pro_docs.item.title {
  font-size: 16px;
  font-weight: bold;
  width: 100%;
  /*img {
        width: 30px;
        height: 30px;
        margin-right: 15px;
      }*/
}
.pro_docs.item.content {
  height: auto;
}
.pro_docs.item.item_text {
  padding: 5px 0 0 20px;
  font-size: 16px;
}
.pro_docs.item.item_text a {
  color: #666666;
  text-decoration: none;
}
.pro_docs.item.item_text a:visited {
  color: #0099FF;
}
.pro_docs.item.item_text a:hover {
  color: #0099FF;
}



