.resolve-parent-height {
  display: block;
  content: '.';
  clear: both;
  line-height: 0;
  visibility: hidden;
}
.banners.main-wrapper {
  width: 100%;
}
.banners.main-wrapper .banner-wrapper {
  width: 100%;
  height: 300px;
  padding-left: 7%;
  padding-right: 14%;
}
.banners.main-wrapper .banner-wrapper:after {
  display: block;
  content: '.';
  clear: both;
  line-height: 0;
  visibility: hidden;
}
.banners.main-wrapper .banner-wrapper .content-wrapper {
  padding-top: 40px;
  width: 51%;
}
.banners.main-wrapper .banner-wrapper .title {
  font-family: ' Bold', '';
  font-weight: 700;
  font-size: 18px;
  font-style: normal;
  color: #666666;
}
.banners.main-wrapper .banner-wrapper .text {
  width: 100%;
  height: 48px;
  font-size: 18px;
  text-align: left;
  white-space: pre-wrap;
}
.banners.main-wrapper .banner-wrapper .button {
  min-width: 100px;
  height: 35px;
  border: none;
  outline: none;
  border-radius: 5px;
  background-color: #5389D2;
  font-size: 14px;
  color: #fff;
  font-weight: 700;
  text-align: center;
  margin-top: 40px;
}
.banners.main-wrapper .banner-wrapper .btn_left {
  margin-right: 15px;
  color: #0069A8;
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid #0069A8;
  transition: 0.3s;
  -moz-transition: 0.3s;
  /* Firefox 4 */
  -webkit-transition: 0.3s;
  /* Safari  Chrome */
  -o-transition: 0.3s;
  /* Opera */
}
.banners.main-wrapper .banner-wrapper .btn_left:hover {
  color: #fff;
  background-color: #0069A8;
}
.banners.main-wrapper .banner-wrapper .btn_right {
  margin-right: 15px;
  color: #fff;
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid #fff;
  transition: 0.3s;
  -moz-transition: 0.3s;
  /* Firefox 4 */
  -webkit-transition: 0.3s;
  /* Safari  Chrome */
  -o-transition: 0.3s;
  /* Opera */
}
.banners.main-wrapper .banner-wrapper .btn_right:hover {
  color: #0069A8;
  background-color: #fff;
}
.banners.main-wrapper .banner-wrapper .pic {
  width: 331px;
  height: 230px;
  margin-right: 20px;
}
.banners.main-wrapper .banner-wrapper.default {
  background-color: #fff;
}
.banners.main-wrapper .banner-wrapper.default .content-wrapper.left {
  float: left;
}
.banners.main-wrapper .banner-wrapper.default .content-wrapper.left .text {
  color: #666;
}
.banners.main-wrapper .banner-wrapper.default .content-wrapper.right {
  float: right;
}
.banners.main-wrapper .banner-wrapper.default .content-wrapper.right .text {
  color: #666;
}
.banners.main-wrapper .banner-wrapper.default .pic.right {
  float: left;
}
.banners.main-wrapper .banner-wrapper.default .pic.left {
  float: right;
}
.banners.main-wrapper .banner-wrapper.dark {
  background-color: #333;
}
.banners.main-wrapper .banner-wrapper.dark .content-wrapper {
  float: right;
}
.banners.main-wrapper .banner-wrapper.dark .content-wrapper .text {
  color: #fff;
}
.banners.main-wrapper .banner-wrapper.dark .pic {
  float: left;
}



