.common-qq {
  /*new right*/
}
.common-qq * {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.common-qq a,
.common-qq img {
  border: 0;
  text-decoration: none;
}
.common-qq body {
  font: 12px/180% Arial, Helvetica, sans-serif, "";
}
.common-qq p {
  margin: 0px;
  padding: 0px;
  line-height: 20px;
}
.common-qq i {
  margin: 0px;
  padding: 0px;
  font-style: normal;
}
.common-qq ul {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
.common-qq ul li {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
.common-qq a {
  color: #333333;
  text-decoration: none;
}
.common-qq a:hover {
  color: #ff5a00;
  text-decoration: none;
}
.common-qq .clear {
  clear: both;
  font-size: 0px;
  line-height: 0;
  height: 0;
}
@font-face {
  font-family: 'icomoon';
  src: url('../../../public/common_logout/fonts/icomoon.eot?qradjf');
  src: url('../../../public/common_logout/fonts/icomoon.eot?qradjf#iefix') format('embedded-opentype'), url('../../../public/common_logout/fonts/icomoon.ttf?qradjf') format('truetype'), url('../../../public/common_logout/fonts/icomoon.woff?qradjf') format('woff'), url('../../../public/common_logout/fonts/icomoon.svg?qradjf#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}
.common-qq .demo-icon {
  font-family: "icomoon";
  font-style: normal;
  font-weight: normal;
  speak: none;
  display: inline-block;
  text-decoration: none;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  font-size: 24px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 24px;
  color: #999;
}
.common-qq.cndns-right {
  position: fixed;
  right: 1px;
  top: 50%;
  margin-top: -100px;
  z-index: 1002;
}
.common-qq .cndns-right-meau {
  position: relative;
}
.common-qq .cndns-right-btn {
  width: 48px;
  height: 48px;
  border: 1px solid #ddd;
  text-align: center;
  display: block;
  margin-bottom: 6px;
  position: relative;
  background-color: #fff;
}
.common-qq .cndns-right-btn span {
  color: #848484;
  font-size: 26px;
  line-height: 48px;
}
.common-qq .cndns-right-btn sup {
  display: block;
  min-width: 24px;
  height: 24px;
  text-align: center;
  line-height: 24px;
  color: #fff;
  border-radius: 50%;
  background-color: #ff6800;
  position: absolute;
  left: -12px;
  top: -12px;
}
.common-qq .cndns-right-btn p {
  color: #696969;
  font-size: 14px;
  line-height: 18px;
  padding-top: 5px;
  display: none;
}
.common-qq .cndns-right-meau:hover .cndns-right-btn span {
  display: none;
}
.common-qq .cndns-right-meau:hover .cndns-right-btn p {
  display: block;
}
.common-qq .meau-car .cndns-right-btn {
  border-color: #ff6800;
  margin-bottom: 20px;
}
.common-qq .meau-car.cndns-right-meau:hover .cndns-right-btn {
  background-color: #ff6800;
}
.common-qq .meau-car.cndns-right-meau:hover .cndns-right-btn span {
  color: #fff;
  display: block;
}
.common-qq .meau-car .cndns-right-btn span {
  color: #ff6800;
}
.common-qq .meau-sev .cndns-right-btn {
  border-color: #696969;
  background: url(../../../public/common_logout/kefu.png) no-repeat #696969 center;
}
.common-qq .meau-sev .cndns-right-btn p {
  color: #fff;
}
.common-qq .meau-sev .cndns-right-btn span {
  color: #fff;
}
.common-qq .meau-top .cndns-right-btn span {
  font-size: 12px;
  line-height: 12px;
  padding-top: 10px;
  display: block;
}
.common-qq .meau-top .cndns-right-btn i {
  display: block;
  color: #999;
}
.common-qq .meau-top.cndns-right-meau:hover .cndns-right-btn {
  background-color: #696969;
}
.common-qq .meau-top.cndns-right-meau:hover .cndns-right-btn span {
  display: block;
  color: #fff;
}
.common-qq .meau-top.cndns-right-meau:hover .cndns-right-btn i {
  color: #fff;
}
.common-qq .cndns-right-box {
  position: absolute;
  top: -15px;
  right: 48px;
  padding-right: 25px;
  display: none;
}
.common-qq .cndns-right-box .box-border {
  border: 1px solid #ccc;
  border-top: 4px solid #696969;
  padding: 20px;
  background-color: #fff;
  -webkit-box-shadow: 0 3px 8px rgba(0, 0, 0, 0.15);
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.15);
  position: relative;
}
.common-qq .cndns-right-box .box-border .arrow-right {
  display: block;
  width: 16.5px;
  height: 16px;
  background: url(../../../public/common_logout/arrow1.png) no-repeat;
  position: absolute;
  right: -13px;
  top: 26px;
}
.common-qq .cndns-right-box .box-border .sev-t span {
  font-size: 42px;
  float: left;
  display: block;
  line-height: 56px;
  margin-right: 20px;
  color: #d3d3d3;
}
.common-qq .cndns-right-box .box-border .sev-t p {
  float: left;
  color: #696969;
  font-size: 24px;
  line-height: 28px;
}
.common-qq .cndns-right-box .box-border .sev-t p i {
  display: block;
  font-size: 14px;
  color: #aaa;
}
.common-qq .cndns-right-box .box-border .sev-b {
  padding-top: 15px;
  margin-top: 15px;
  border-top: 1px solid #e4e4e4;
}
.common-qq .cndns-right-box .box-border .sev-b h4 {
  color: #666;
  font-size: 14px;
  font-weight: normal;
  padding-bottom: 15px;
}
.common-qq .cndns-right-box .box-border .sev-b li {
  float: left;
  width: 33.33333%;
}
.common-qq .cndns-right-box .box-border .sev-b li a {
  display: inline-block;
  color: #999;
  font-size: 13px;
  padding-left: 43px;
  background: url(../../../public/common_logout/q2.png) no-repeat left 3px;
  line-height: 36px;
}
.common-qq .cndns-right-box .box-border .sev-b li a:hover {
  color: #696969;
}
.common-qq .meau-sev .cndns-right-box .box-border {
  width: 430px;
}
.common-qq .meau-contact .cndns-right-box .box-border {
  width: 255px;
}
.common-qq .meau-contact .cndns-right-btn {
  background: url(../../../public/common_logout/call.png) no-repeat white center;
}
.common-qq .meau-contact:hover .cndns-right-btn {
  background: white;
}
.common-qq .cndns-right-meau:hover .cndns-right-box {
  display: block;
}
.common-qq .meau-code .cndns-right-box {
  top: inherit;
  bottom: -35px;
}
.common-qq .meau-code .cndns-right-box .box-border {
  width: 156px;
  text-align: center;
  border-top: 1px solid #ccc;
}
.common-qq .meau-code .cndns-right-box .box-border i {
  display: block;
  color: #696969;
  font-size: 16px;
  line-height: 16px;
}
.common-qq .meau-code .cndns-right-box .box-border .arrow-right {
  top: inherit;
  bottom: 50px;
}
.common-qq .meau-sev .cndns-right-btn .demo-icon {
  display: none;
}
.common-qq .meau-sev:hover .cndns-right-btn {
  background: #696969;
}
.common-qq .meau-zs .cndns-right-btn {
  background-color: #ff6800;
  color: #fff;
  margin-top: 80px;
  border-color: #ff6800;
}
.common-qq .meau-zs .cndns-right-btn span {
  color: #fff;
}
.common-qq .meau-zs .cndns-right-btn p {
  color: #fff;
}
.common-qq .zxkf {
  background: url("../../../public/common_logout/kefu2.png") no-repeat;
}
.common-qq .kfrx {
  background: url("../../../public/common_logout/call2.png") no-repeat;
}



