.resolve-parent-height {
  display: block;
  content: '.';
  clear: both;
  line-height: 0;
  visibility: hidden;
}
.index.index-feature.main-wrapper {
  width: 100%;
  height: 200px;
}
.index.index-feature.main-wrapper .feature-list {
  position: relative;
}
.index.index-feature.main-wrapper .feature-list:after {
  display: block;
  content: '.';
  clear: both;
  line-height: 0;
  visibility: hidden;
}
.index.index-feature.main-wrapper .feature-list .feature-item {
  width: 20%;
  height: 200px;
  float: left;
  border-right: 1px solid #fff;
  border-left: 1px solid #dedede;
  text-align: center;
  background-color: #f1f4f9;
}
.index.index-feature.main-wrapper .feature-list .feature-item.hidden .pic {
  width: 60px;
  height: 50px;
  margin-top: 50px;
  margin-bottom: 20px;
}
.index.index-feature.main-wrapper .feature-list .feature-item.hidden .title {
  width: 100%;
  color: #666;
  font-size: 18px;
  text-align: center;
  font-weight: 400;
}
.index.index-feature.main-wrapper .feature-list .feature-item.hidden .text {
  display: none;
}
.index.index-feature.main-wrapper .feature-list .feature-item.show .top {
  display: -ms-flexbox;
  display: flex;
  padding-top: 10px;
  width: 100%;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.index.index-feature.main-wrapper .feature-list .feature-item.show .top .pic {
  width: 48px;
  height: 40px;
  margin: 8px 0;
}
.index.index-feature.main-wrapper .feature-list .feature-item.show .top .title {
  display: inline-block;
  font-size: 16px;
  color: #666;
  text-align: left;
}
.index.index-feature.main-wrapper .feature-list .feature-item.show .text {
  padding: 0 10px;
  color: #999999;
  text-align: center;
  margin-top: 15px;
  overflow: hidden;
  max-height: 100px;
  white-space: pre-wrap;
}



